body {
    margin-top: 9px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    background: #0F1B07;
}

.header__title {
    text-align: center;
    font-size: 3em;
}

.cell {
    height: 12px;
    width: 12px;
    border: 1px inset #555;
    background: white;
}

.cell-dead {
    background: black;
}

.grid {
    display: flex;
    flex-direction: row;
    line-height: 0;
    margin: auto;
    margin-top: 2em;
    margin-bottom: 2em;
    border: 3px solid #555
}

.main-layout {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.button-container {
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-wrap: wrap;
}

.set-container {}

.button-container button {
    padding: 2px;
    padding: 5px;
    outline: none;
    cursor: pointer;
    background: #C6D166;
    width: 6em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 1.3em;
    text-transform: capitalize;
    border-radius: 10px;
    border: none;
    transition: background-color .1s ease-in-out;
    margin-right: 1em;
    margin-bottom: 1em;
}

.button-container button:hover,
.button-container button:active {
    box-shadow: 0 1px 1px 0 #cfcfcf, 0 2px 5px 0 #cfcfcf;
}

.button-active {
    background: #0F1B07;
}

.generation {
    margin-bottom: 15px;
    font-size: 20px;
}